@use '../base/mixins' as mixin;

@import '../blocks/_banner-section';
@import '../blocks/_investors';
@import '../blocks/_review';
@import '../blocks/_get-started';
@import '../blocks/_faq';

.world-data {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 48px 24px;

  @include mixin.laptop {
    gap: 32px 24px;
  }

  @include mixin.small-tablet {
    gap: 24px 24px;
  }

  &__head {
    width: 100%;

    .subtitle {
      margin: 0 auto;
      max-width: 850px;
    }
  }

  &__map {
    max-width: 48.75%;

    @include mixin.laptop {
      max-width: 57.5%;
    }

    @include mixin.small-tablet {
      max-width: unset;
    }

    img {
      width: 100%;
      display: block;
    }
  }

  &__data {
    display: flex;
    flex-direction: column;
    gap: 40px;
    max-width: 42.7%;

    @include mixin.laptop {
      max-width: 38.8%;
    }

    @include mixin.tablet {
      gap: 25.12px;
    }

    @include mixin.small-tablet {
      max-width: unset;
    }
  }

  &__data-item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 8px 32px;

    @include mixin.small-laptop {
      flex-direction: column;
      align-items: flex-start;
    }

    .breadcrumbs {
      justify-content: flex-end;
      max-width: 250px;

      @include mixin.small-laptop {
        max-width: unset;
        justify-content: flex-start;
      }
    }
  }

  &__text {
    width: 100%;

    p {
      margin: 0 auto;
      max-width: 1004px;
      text-align: center;
      font-size: 14px;
    }
  }
}

.section-equity {
  &__subtitle {
    font-size: 20px;
    font-weight: 300;
    margin-bottom: 8px;

    @include mixin.tablet {
      margin-bottom: 16px;
    }
  }

  .section__head {
    margin-bottom: 48px;

    @include mixin.small-tablet {
      margin-bottom: 24px;
    }
  }
}

.equity {
  border-radius: 32px;
  border: 1px solid var(--secondary-light, #eee);
  padding: 48px 24px;
  text-align: center;
  min-height: 100%;

  &__icon {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    margin-bottom: 24px;
  }

  &--blue &__icon {
    background: var(--primary-lightest, #ecf6ff);
  }

  &--orange &__icon {
    background: var(--warning-lightest, #fcf2ea);
  }

  .h3-custom {
    margin-bottom: 8px;
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
  }
}

.section-needs {
  .section__head {
    max-width: 960px;
    margin: 0 auto;
    margin-bottom: 48px;

    @include mixin.small-tablet {
      margin-bottom: 24px;
    }
  }
}

.blank {
  overflow: hidden;
  padding: 32px 44px;
  padding-bottom: 0;
  border-radius: 32px;
  background: var(--secondary-lighter, #f5f5f5);
  text-align: center;

  @include mixin.small-tablet {
    padding: 32px 14px;
    padding-bottom: 0;
  }

  &--big {
    padding: 40px 40px;
    padding-bottom: 40px;

    @include mixin.small-tablet {
      padding: 24px 14px;
    }
  }

  &__title {
    margin-bottom: 8px;
  }

  &--big &__title {
    margin-bottom: 12px;
  }

  &__text {
    margin-bottom: 32px;

    @include mixin.small-tablet {
      font-size: 14px;
      margin-bottom: 24px;
    }
  }

  &__img {
    box-shadow: 0 27.04px 36.06px 0 rgba(0, 0, 0, 0.08);

    img {
      width: 100%;
    }
  }
}

.dirs {
  .row {
    margin-bottom: 32px;

    @include mixin.phone {
      margin-bottom: 24px;
    }
  }

  &__btn {
    background: var(--secondary-lightest, #fff);
    min-width: 186px;

    @include mixin.phone {
      min-width: 100%;
    }
  }
}

.dir {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 16px;
  padding: 12px;
  border-radius: 16px;
  background: var(--secondary-lightest, #fff);

  &__icon {
    min-width: 51px;
    width: 51px;
    height: 51px;
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--primary-lightest, #ecf6ff);

    @include mixin.tablet {
      min-width: 40px;
      width: 40px;
      height: 40px;

      svg {
        width: 24px;
        height: 24px;
      }
    }
  }

  &__data {
    text-align: left;
  }

  &__text {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.5;
  }

  &__title {
    transition: 0.3s linear;

    @include mixin.tablet {
      font-size: 14px;
    }
  }

  &--active &__title {
    color: var(--primary-darker, #1874c1);
  }
}

.about-reviews {
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 48px;

    @include mixin.small-laptop {
      gap: 24px;
    }
  }

  &__btn {
    min-width: 200px;

    @include mixin.phone {
      min-width: 150px;
    }
  }
}